<template>
  <div :class="$style.content">
    <div :class="$style.title">Agregar gasto</div>
    <div :class="$style.currency"><Currency :amount="amount"/></div>
    <div :class="[$style.row, $style.categories]">
      <span
        :class="[$style.item, cat === category && $style.itemSelected]"
        v-for="cat in allCategories" :key="cat"
        @click="() => category = cat">{{ cat }}</span>
      <span
        :class="[$style.item]"
        @click="addOther">Otro</span>
    </div>
    <input
      :class="$style.input"
      :disabled="loading"
      type="date"
      v-model="date"
      placeholder="DD/MM/YYYY"/>
    <Keyboard v-model="amount"/>
    <br>
    <button
      :disabled="!ready || loading"
      @click="add">Agregar</button>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import moment from 'moment';

import Keyboard from '@/components/Keyboard.vue';
import Currency from '@/components/Currency.vue';

export default {
  name: 'Add',
  components: {
    Keyboard,
    Currency,
  },
  computed: {
    ...mapGetters(['categories']),
    allCategories() {
      return [...this.categories, ...(this.category ? [this.category] : [])]
        .reduce((prev, curr) => [...prev, ...(prev.includes(curr) ? [] : [curr])], []);
    },
    ready() {
      return [
        this.amount,
        this.category,
        this.date,
      ].reduce((prev, curr) => prev && curr !== undefined && curr !== '', true);
    },
  },
  data() {
    return {
      loading: false,
      amount: '',
      category: undefined,
      date: moment().format('YYYY-MM-DD'),
    };
  },
  methods: {
    ...mapActions(['addSpend']),
    addOther() {
      /* eslint-disable */
      const newCategory = prompt('nombre');
      /* eslint-enable */
      this.category = newCategory;
    },
    add() {
      this.loading = true;
      const payload = {
        amount: parseInt(this.amount.replace('.', '').replace('$', ''), 10),
        category: this.category,
        date: this.date,
      };
      this.addSpend(payload).then(() => {
        this.loading = false;
        this.$router.push('/home');
      });
    },
  },
};
</script>

<style module lang="scss">
.content {
  display: flex;
  flex-direction: column;
}
.title {
  margin-top: 1em;
  font-size: 1.2em;
}
.currency {
  margin: 0.2em;
  font-size: 2em;
}
.row {
  display: flex;
  flex-direction: row;
}
.item {
  display: flex;
  font-size: 1.25em;
  margin: 5px;
  color: white;
  background: var(--primary-color);
  border: 3px solid var(--primary-color);
  border-radius: 4px;
  opacity: 0.2;
  padding: 0 2px;
}
.itemSelected {
  opacity: 1;
  border: 3px solid var(--secondary-color);
}
.input {
  width: 98%;
  font-size: 2em;
  margin: 0.3em auto;
  height: 2em;
}
.categories {
  flex-wrap: wrap;
  justify-content: center;
}
</style>
