<template>
  <div :class="$style.content">
    <PieChart :chart-data="monthSpendsPieData"/>
  </div>
</template>

<script>
import moment from 'moment';
import { mapGetters } from 'vuex';
import PieChart from '@/components/PieChart.vue';
import { colorScheme } from '@/utils';

export default {
  name: 'MonthPie',
  props: {
    month: Number,
  },
  components: {
    PieChart,
  },
  computed: {
    ...mapGetters(['spends']),
    monthSpendsPieData() {
      const processed = this.spends
        .filter(x => moment(x.date).month() === this.month)
        .reduce((prev, curr) => Object.assign(
          prev,
          (
            prev[curr.category] !== undefined
            ? { [curr.category]: curr.amount + prev[curr.category] }
            : { [curr.category]: curr.amount }),
          ),
          {});
      return {
        datasets: [{
          data: Object.values(processed),
          backgroundColor: colorScheme.slice(0, processed.length),
      }],
      // These labels appear in the legend and in the tooltips when hovering different arcs
      labels: Object.keys(processed),
      };
    },
  },
};
</script>

<style module lang="scss">
.content {
  display: flex;
  flex-direction: column;
  padding: 0 0.3em;
  color: #2c3e50;
}
</style>
