<template>
  <div :class="$style.content">
    <div :class="$style.title">Detalle ahorros</div>
    <div :class="$style.main"><Currency :amount="sumInvestment"/></div>
    <card>
      <div :class="$style.col" v-for="i in institutions" :key="i">
        <div :class="[$style.row, $style.header]">{{ i }}</div>
        <div :class="[$style.row, $style.item]" v-for="val in Object.keys(moreRecentInvestment[i])" :key="val">
          <input type="checkbox" v-model="selected" :value="`${i}-${val}`">
          {{ val }}
          <Currency :amount="moreRecentInvestment[i][val]"/>
        </div>
      </div>
    </card>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

import Currency from '@/components/Currency.vue';
import Card from '@/components/Card.vue';

export default {
  name: 'Month',
  components: {
    Currency,
    Card,
  },
  data() {
    return {
      selected: [
        'cuprum-Ahorro Voluntario',
        'plan_vital-Ahorro Voluntario',
        'scotiabank-Cuenta Renta Diaria',
        'modelo-Cuenta Ahorro Voluntario',
      ],
    };
  },
  computed: {
    ...mapGetters([
      'moreRecentInvestment',
    ]),
    institutions() {
      return Object.keys(this.moreRecentInvestment).filter(x => x !== 'date');
    },
    sumInvestment() {
      let total = 0;
      this.selected.forEach(e => {
        const [inst, prod] = e.split('-');
        total += this.moreRecentInvestment[inst][prod];
      });
      return total;
    },
  },
};
</script>

