<script>
import { Pie, mixins } from 'vue-chartjs';
const { reactiveProp } = mixins;
import { formatPrice } from '@/utils';

export default {
  name: 'PieChart',
  extends: Pie,
  mixins: [reactiveProp],
  data() {
    return {
      options: {
        responsive: true,
        maintainAspectRatio: false,
        legend: {
          display: true,
          position: 'bottom',
        },
        tooltips: {
          enabled: true,
          intersect: false,
          mode: 'index',
          callbacks: {
            label: (x, y) => {
              const total = y.datasets[0].data.reduce((prev, curr) => prev + curr, 0);
              const percentage = Math.floor(100*y.datasets[0].data[x.index]/total);
              const amount = formatPrice(y.datasets[0].data[x.index]);
              return `${percentage}% (${amount})`;
            },
            beforeLabel: (x, y) => {
              return y.labels[x.index];
            },
          },
        },
      },
    };
  },
  mounted () {
    this.renderChart(this.chartData, this.options);
  },
};
</script>

<style module lang="scss">
.content {
  font-size: 3em;
}
</style>
