<template>
  <div :class="$style.content">
    <div :class="$style.main"><Currency :amount="totalMonthlySpend"/></div>
    <div :class="$style.row">
      <span>Gastos {{ moment().format('dddd DD/MM') }}</span><Currency :amount="todayDailySpend"/>
    </div>
  </div>
</template>

<script>
import moment from 'moment';
import { mapGetters } from 'vuex';
import Currency from '@/components/Currency.vue';

export default {
  name: 'SpendSummary',
  components: {
    Currency,
  },
  computed: {
    ...mapGetters(['spends', 'todayDailySpend']),
    totalMonthlySpend() {
      return this.spends
        .filter(x => moment(x.date).isSame(moment(), 'M'))
        .reduce((prev, curr) => prev + curr.amount, 0);
    },
  },
  methods: {
    moment,
  },
};
</script>

