<template>
  <div :class="$style.content">
    <span :class="$style.title">{{titleToShow}}</span>
    <div :class="[$style.row, $style.headerRow]">
      <div :class="$style.item">Cuándo</div>
      <div :class="$style.item">Cuánto</div>
      <div :class="$style.item">Dónde</div>
    </div>
    <div :class="$style.row" v-for="s in spendsToShow" :key="`${s.date}-${s.amount}-${s.category}`">
      <div :class="$style.item">{{ s.date }}</div>
      <div :class="$style.item">{{ s.amount | formatPrice }}</div>
      <div :class="$style.item">{{ s.category }}</div>
    </div>
    <div v-if="limited" :class="$style.link"><router-link to="/month">Ver todo</router-link></div>
  </div>
</template>

<script>
import moment from 'moment';
import { mapGetters } from 'vuex';
import { formatPrice } from '@/utils';

export default {
  name: 'SpendsList',
  props: {
    month: {
      type: Number,
      default: moment().month(),
    },
    limited: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      total: 4,
    };
  },
  computed: {
    ...mapGetters(['spends']),
    monthlySpends() {
      return this.spends.filter(x => moment(x.date).month() === this.month);
    },
    titleToShow() {
      if (this.limited) {
        return `Últimos ${this.total} gastos`;
      }
      return 'Todos los gastos';
    },
    spendsToShow() {
      if (this.limited) {
        return [...this.monthlySpends].splice(0, this.total);;
      }
      return [...this.monthlySpends];
    },
  },
  filters: {
    formatPrice,
  },
};
</script>

