<script>
import { Bar, mixins } from 'vue-chartjs';
const { reactiveProp } = mixins;
import { formatPrice } from '@/utils';

export default {
  name: 'BarChart',
  extends: Bar,
  mixins: [reactiveProp],
  data() {
    return {
      options: {
        responsive: true,
        maintainAspectRatio: false,
        legend: { display: false },
        tooltips: {
          enabled: true,
          intersect: false,
          mode: 'index',
          callbacks: {
            label: tooltipItem => formatPrice(tooltipItem.yLabel, 0),
          },
        },
        scales: {
          xAxes: [{
            gridLines: {
              borderDash: [4, 4],
              color: '#F1F1F1',
            },
            stacked: true,
          }],
          yAxes: [{
            gridLines: {
              borderDash: [4, 4],
              color: '#F1F1F1',
            },
            stacked: true,
            ticks: {
              callback: value => formatPrice(value),
            },
          }],
        },
      },
    };
  },
  mounted () {
    this.renderChart(this.chartData, this.options);
  },
};
</script>

<style module lang="scss">
.content {
  font-size: 3em;
}
</style>
