<template>
  <div :class="$style.content">
    <div :class="$style.title">Gastos del Mes</div>
    <input
      :class="$style.input"
      type="month"
      v-model="selectedDate"
      placeholder="DD/MM/YYYY"/>
    <div :class="$style.main"><Currency :amount="totalMonthlySpend"/></div>
    <card><SpendsList :month="month"/></card>
    <card><MonthPie :month="month" /></card>
  </div>
</template>

<script>
import moment from 'moment';
import { mapGetters } from 'vuex';

import SpendsList from '@/components/SpendsList.vue';
import MonthPie from '@/components/MonthPie.vue';
import Currency from '@/components/Currency.vue';
import Card from '@/components/Card.vue';

export default {
  name: 'Month',
  components: {
    SpendsList,
    MonthPie,
    Currency,
    Card,
  },
  data() {
    return {
      selectedDate: moment().format('YYYY-MM'),
    };
  },
  computed: {
    ...mapGetters(['spends']),
    totalMonthlySpend() {
      return this.spends
        .filter(x => moment(x.date).month() === this.month)
        .reduce((prev, curr) => prev + curr.amount, 0);
    },
    month() {
      return moment(this.selectedDate).month();
    }
  },
};
</script>

